
.money {
  height: calc(100vh - 10rem);
  overflow: auto;
}
.money .detailPart {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 1rem 3rem;
    background: #1AAD19;
}
.money .detailPart .timePicker {
      width: 4rem;
      padding-right: 2rem;
      border-right: 0.1rem dotted white;
}
.money .detailPart .moneyDetail {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding-left: 2rem;
}
.money .detailPart .moneyDetail .income {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.money .detailPart .top {
      color: #eddfdf;
}
.money .detailPart .bottom {
      color: white;
      height: 24px;
      line-height: 24px;
}
.money .shoppingicon {
    display: inline-block;
    width: 4rem;
    height: 4rem;
    text-align: center;
    line-height: 4rem;
    border-radius: 50%;
    background: #f8f5f5;
    border: 1px solid #f8f5f5;
    margin: 0.5rem 1rem 0.5rem -0.8rem;
    color: #ffbb19;
}
.money .outcome {
    color: red !important;
}
.money .addIcon {
    position: fixed;
    bottom: 0px;
}
.money .tabPart {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /* border: 1px solid red; */
    margin: 1.2rem;
    color: white;
}
.money .tabPart .part {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      border: 1px solid #1AAD19;
      color: #1AAD19;
      text-align: center;
}
.money .tabPart .hightlight {
      background: #1AAD19;
      color: white;
}
.money .fixBottom {
    position: fixed;
    bottom: 0px;
    width: 100%;
}
.money .iconfont {
    font-size: 2.5rem;
}



