
.DatePlan .headerPart {
  padding-bottom: 2rem;
  color: red;
  text-align: center;
}
.DatePlan .headerPart .myTitle {
    color: #3cc51f;
    font-size: 3.2rem;
    font-weight: 400;
}
.DatePlan .headerPart .info {
    color: gray;
    font-size: 1.7rem;
}
.DatePlan .headerPart .textPart {
    margin-left: 15px;
    border: none;
    border-bottom: 1px solid gray;
    height: 32px;
    line-height: 32px;
    outline: none;
    width: 80%;
}
.DatePlan .add_icon {
  font-size: 2.6rem;
  position: absolute;
  right: 17px;
  line-height: 32px;
}
.DatePlan .doneTag {
  text-decoration: line-through;
}
.DatePlan .weui-cell__bd input {
  width: 89%;
}
.DatePlan .weui-cell__bd span {
  display: inline-block;
  width: 89%;
}
.DatePlan .weui-cell {
  -webkit-transition: 250ms ease-out;
  transition: 250ms ease-out;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scalex(1);
          transform: scalex(1);
  -webkit-transform: translate(100%, 1);
          transform: translate(100%, 1);
  opacity: 1;
}
.DatePlan .fixBottom {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.DatePlan .showDialog {
  opacity: 1 !important;
  display: block !important;
}
.DatePlan .hideDialog {
  opacity: 0;
  display: none;
}
.DatePlan .showDetail {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}



