
.coin {
  /* position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; */
  z-index: -1;
}
.coin .headerPart {
    padding-bottom: 4rem;
    color: red;
    text-align: center;
}
.coin .headerPart .myTitle {
      color: #3cc51f;
      font-size: 3.6rem;
      font-weight: 400;
}
.coin .headerPart .info {
      color: gray;
      font-size: 1.6rem;
      text-align: center;
      width: 80%;
      margin: 0 auto;
}
.coin .iconPart {
    position: relative;
    width: 100%;
    height: 13rem;
    text-align: center;
    padding-top: 1rem;
}
.coin .iconPart .leftCircle {
      margin: 0 auto;
      position: absolute;
      width: 9rem;
      height: 9rem;
      border: 1.5rem solid #DE7C23;
      border-radius: 50%;
      clip: rect(0, 6rem, 12rem, 0);
      left: 50%;
      margin-left: -6rem;
}
.coin .iconPart .topCircle {
      margin: 0 auto;
      clip: rect(0, 4.5rem, 9rem, 0);
      margin-left: -4.5rem;
      position: absolute;
      width: 6rem;
      height: 6rem;
      left: 50%;
      border: 1.5rem solid #E7A060;
      border-radius: 50%;
      margin-top: 1.5rem;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
}
.coin .iconPart .infoText {
      width: 3.5rem;
      height: 3.5rem;
      position: absolute;
      left: 50%;
      text-align: center;
      margin-top: 4.5rem;
      margin-left: -1.75rem;
      color: #E7A061;
      cursor: pointer;
      font-size: 2rem;
}
.coin .iconPart .leftS {
      -webkit-transform: rotate(1080deg);
              transform: rotate(1080deg);
      -webkit-transition: -webkit-transform 3s ease;
      transition: -webkit-transform 3s ease;
      transition: transform 3s ease;
      transition: transform 3s ease, -webkit-transform 3s ease;
}
.coin .iconPart .leftT {
      -webkit-transform: rotate(-1080deg);
              transform: rotate(-1080deg);
      -webkit-transition: -webkit-transform 3s ease;
      transition: -webkit-transform 3s ease;
      transition: transform 3s ease;
      transition: transform 3s ease, -webkit-transform 3s ease;
}
.coin .iconPart .rightS {
      -webkit-transform: rotate(1260deg);
              transform: rotate(1260deg);
      -webkit-transition: -webkit-transform 3s ease;
      transition: -webkit-transform 3s ease;
      transition: transform 3s ease;
      transition: transform 3s ease, -webkit-transform 3s ease;
}
.coin .iconPart .rightT {
      -webkit-transform: rotate(-1260deg);
              transform: rotate(-1260deg);
      -webkit-transition: -webkit-transform 3s ease;
      transition: -webkit-transform 3s ease;
      transition: transform 3s ease;
      transition: transform 3s ease, -webkit-transform 3s ease;
}
.coin .footerPart {
    padding: 1rem 2rem;
    overflow: auto;
    margin-top: 30px;
}
.coin .footerPart .yes {
      float: left;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      background: #E7A060;
      color: #fff;
      text-align: center;
      line-height: 4rem;
      font-size: 1.4rem;
}
.coin .footerPart .no {
      float: right;
      width: calc(4rem - 2px);
      height: calc(4rem - 2px);
      border-radius: 50%;
      border: 2px solid #ccc;
      background: #fff;
      text-align: center;
      line-height: calc(4rem - 2px);
      font-size: 1.4rem;
}



