
.memo .headerPart {
  padding-bottom: 2rem;
  color: red;
  text-align: center;
}
.memo .headerPart .myTitle {
    color: #3cc51f;
    font-size: 3.2rem;
    font-weight: 400;
}
.memo .headerPart .info {
    color: gray;
    font-size: 1.6rem;
}
.memo .editPart {
  text-align: center;
  padding: 10px 15px;
}
.memo .editPart input {
    border: none;
    outline: none;
    height: 3rem;
    border-bottom: 1px solid #3cc51f;
    width: 100%;
    font-size: 1.6rem;
}
.memo .fixBottom {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.memo .showDialog {
  opacity: 1 !important;
  display: block !important;
}
.memo .hideDialog {
  opacity: 0;
  display: none;
}



