
.editFood .headerPart {
  padding: 4rem 0;
  color: red;
  text-align: center;
}
.editFood .headerPart .myTitle {
    color: #3cc51f;
    font-size: 34px;
    font-weight: 400;
}
.editFood .headerPart .info {
    color: gray;
    font-size: 1.7rem;
}
.editFood .editPart {
  text-align: center;
}
.editFood .editPart input {
    border: none;
    outline: none;
    border-bottom: 1px solid green;
    width: 90%;
    font-size: 1.6rem;
    padding-bottom: 5px;
}
.editFood .fixBottom {
  position: fixed;
  bottom: 0px;
  width: 100%;
}



