
.dropdown {
  position: relative;
  display: inline-block;
  height: 40px;
}
.dropdown .more-btn {
    width: 40px;
}
.dropdown .dropdown-list {
    position: absolute;
    padding: 0;
    margin: 0;
    left: -110px;
    text-align: left;
    top: 40px;
    overflow: hidden;
    -webkit-box-shadow: 0 -2px 4px #222;
            box-shadow: 0 -2px 4px #222;
}
.dropdown .dropdown-list .dropdown-list-item {
      padding-left: 10px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background: #2e3238;
      list-style: none;
      display: block;
      width: 150px;
      height: 30px;
      line-height: 30px;
      color: #fbfbfb;
      border-top: 1px solid #444;
}



