
.editName .headerPart {
  padding: 4rem 0;
  color: red;
  text-align: center;
}
.editName .headerPart .myTitle {
    color: #3cc51f;
    font-size: 34px;
    font-weight: 400;
}
.editName .headerPart .info {
    color: gray;
    font-size: 1.7rem;
}
.editName .editPart {
  text-align: center;
}
.editName .editPart input {
    border: none;
    outline: none;
    border-bottom: 1px solid #3cc51f;
    width: 90%;
    font-size: 1.7rem;
}
.editName .fixBottom {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.editName .iconPart {
  padding: 1rem;
}
.editName .iconPart img {
    width: 2rem;
    height: 2rem;
}



