
.home .mint-header {
  background-color: #2e3238;
}
.home .head-field {
  text-align: center;
  padding: 20px 0;
}
.home .head-field .head-field-pic {
    display: inline-block;
    width: 100%;
}
.home .head-field .head-field-pic .img-hover {
      display: inline-block;
      width: 30%;
      height: 0;
      padding-bottom: 30%;
      border-radius: 50%;
      overflow: hidden;
}
.home .grid {
  word-spacing: -6px;
  position: relative;
}
.home .grid:before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    border-top: 1px solid #D9D9D9;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
    top: -1px;
}
.home .grid .grid-item {
    display: inline-block;
    text-align: center;
    width: 33.33%;
    padding: 20px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}
.home .grid .grid-item .grid-item-text {
      display: inline-block;
      padding-top: 10px;
}
.home .grid .grid-item .iconfont {
      font-size: 2.4rem;
}
.home .grid .grid-item:before {
      content: " ";
      position: absolute;
      right: 0;
      top: 0;
      width: 1px;
      height: 100%;
      border-right: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform: scaleX(0.5);
      transform: scaleX(0.5);
      right: -1px;
}
.home .grid .grid-item:after {
      content: " ";
      position: absolute;
      left: 0;
      width: 100%;
      height: 1px;
      border-top: 1px solid #D9D9D9;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
      bottom: -1px;
}



