
@charset "UTF-8";
.info {
  /* 隐藏的输入框 */
}
.info .slide-enter-active,
  .info .slide-leave-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.info .slide-enter,
  .info .slide-leave-to {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
}
.info .slide-1-enter-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity 1s, -webkit-transform 1s;
    transition: opacity 1s, -webkit-transform 1s;
    transition: transform 1s, opacity 1s;
    transition: transform 1s, opacity 1s, -webkit-transform 1s;
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.info .slide-1-leave-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity .5s, -webkit-transform .5s;
    transition: opacity .5s, -webkit-transform .5s;
    transition: transform .5s, opacity .5s;
    transition: transform .5s, opacity .5s, -webkit-transform .5s;
}
.info .slide-1-enter,
  .info .slide-1-leave-to {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
.info .slide-2-enter-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity 1s, -webkit-transform 1s;
    transition: opacity 1s, -webkit-transform 1s;
    transition: transform 1s, opacity 1s;
    transition: transform 1s, opacity 1s, -webkit-transform 1s;
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}
.info .slide-2-leave-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity .5s, -webkit-transform .5s;
    transition: opacity .5s, -webkit-transform .5s;
    transition: transform .5s, opacity .5s;
    transition: transform .5s, opacity .5s, -webkit-transform .5s;
}
.info .slide-2-enter,
  .info .slide-2-leave-to {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
.info .slide-3-enter-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity 1s, -webkit-transform 1s;
    transition: opacity 1s, -webkit-transform 1s;
    transition: transform 1s, opacity 1s;
    transition: transform 1s, opacity 1s, -webkit-transform 1s;
    -webkit-transition-delay: 0.4s;
            transition-delay: 0.4s;
}
.info .slide-3-leave-active {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    -webkit-transition: opacity .5s, -webkit-transform .5s;
    transition: opacity .5s, -webkit-transform .5s;
    transition: transform .5s, opacity .5s;
    transition: transform .5s, opacity .5s, -webkit-transform .5s;
}
.info .slide-3-enter,
  .info .slide-3-leave-to {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
}
.info .head-field {
    text-align: center;
    padding: 20px 0;
    margin-top: 50px;
}
.info .head-field .head-field-pic {
      display: inline-block;
      width: 100%;
}
.info .head-field .head-field-pic .img-hover {
        display: inline-block;
        width: 30%;
        height: 0;
        padding-bottom: 30%;
        border-radius: 50%;
        overflow: hidden;
}
.info .info-field {
    text-align: center;
    font-size: 16px;
    color: #999;
}
.info .hiddenInput {
    opacity: 0;
    position: absolute;
}



