
.selectIcon {
  padding: 4rem 2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.selectIcon .tabPart {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 1.2rem 2rem;
    color: white;
    position: fixed;
    top: 4rem;
    z-index: 20;
    width: 100%;
    background: #a9f0a9;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    left: 0;
}
.selectIcon .tabPart .part {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      border: 1px solid #1AAD19;
      color: #1AAD19;
      text-align: center;
}
.selectIcon .tabPart .hightlight {
      background: #1AAD19;
      color: white;
}
.selectIcon .shoppingicon {
    display: inline-block;
    width: 4rem;
    height: 4rem;
    text-align: center;
    line-height: 4rem;
    border-radius: 50%;
    background: #f8f5f5;
    border: 1px solid #f8f5f5;
    margin: 0.5rem 1rem 0.5rem -1.5rem;
    color: gray;
}
.selectIcon .shoppingicon i {
      font-size: 2.5rem;
      color: green;
}
.selectIcon .consumeList {
    margin-top: 4rem;
}
.selectIcon .fixBottom {
    position: fixed;
    bottom: 0px;
    width: 100%;
    left: 0px;
}
.selectIcon .showDialog {
    opacity: 1 !important;
    display: block !important;
}
.selectIcon .hideDialog {
    opacity: 0;
    display: none;
}



