
.color-panel {
  width: 100%;
  padding: 10px;
}
.color-panel .color-items {
    display: inline-block;
    width: 100%;
}
.color-panel .color-item {
    display: inline-block;
    width: 20%;
    text-align: center;
    margin: 10px 0;
}
.color-panel .color-item .color-cycle {
      display: inline-block;
      width: 4.5rem;
      height: 4.5rem;
      border-radius: 50%;
      -webkit-box-shadow: 2px 2px 5px #666;
              box-shadow: 2px 2px 5px #666;
}
.color-panel .bg-color1 {
    background-color: #f04134;
}
.color-panel .bg-color2 {
    background-color: #00a854;
}
.color-panel .bg-color3 {
    background-color: #108ee9;
}
.color-panel .bg-color4 {
    background-color: #f5317f;
}
.color-panel .bg-color5 {
    background-color: #f56a00;
}
.color-panel .bg-color6 {
    background-color: #7265e6;
}
.color-panel .bg-color7 {
    background-color: #ffbf00;
}
.color-panel .bg-color8 {
    background-color: #00a2ae;
}
.color-panel .bg-color9 {
    background-color: #2e3238;
}



