
@charset "UTF-8";
.menuCover {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  background: #2c3b41;
  opacity: .5;
  z-index: 1111；;
}
.togglehide span {
  display: none;
}
.togglehide .list-title {
  display: none;
}
.togglehide .menu {
  padding-top: 50px;
}
.togglehide i {
  font-size: 18px;
}
.togglehide .menuCover {
  display: none;
}
.sideBar {
  padding: 20px 0px;
  min-width: 200px;
  background: black;
  position: absolute;
  top: 0px;
  bottom: 0px;
  z-index: 1000;
  -webkit-transition: all ease .4s;
  transition: all ease .4s;
  overflow: auto;
  font-size: 16px;
}
.sideBar span {
    font-size: 1.2rem;
}
.sideBar i {
    font-size: 1.2rem;
}
.toggleIcon {
  width: 50px;
  height: 50px;
  background: red;
  position: absolute;
  top: 0px;
  cursor: pointer;
  -webkit-transition: all ease .4s;
  transition: all ease .4s;
}
.menu {
  font-size: 14px;
}
.menu li {
    list-style: none;
}
.menu li > a {
      color: #b8c7ce;
      width: 100%;
      display: inline-block;
      padding: 10px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      position: relative;
      -webkit-transition: border-left ease .3s;
      transition: border-left ease .3s;
}
.menu a {
    text-decoration: none;
    color: gray;
}
.menu .list-title {
    padding: 9px;
    color: white;
}
.menu .treeview {
    background: #1a2226;
    -webkit-transition: all ease .3s;
    transition: all ease .3s;
}
.menu .treeview > a {
      color: #b8c7ce;
      width: 100%;
      display: inline-block;
      padding: 10px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      -webkit-transition: border-left ease .3s;
      transition: border-left ease .3s;
      position: relative;
}
.menu .treeview > a:hover {
        background: black;
        color: white !important;
        border-left: 3px solid white;
}
.menu .treeview .pull-right-container {
      position: absolute;
      margin-top: -7px;
      right: 20px;
      top: 50%;
}
.menu .treeview-menu {
    background: #2c3b41;
    -webkit-transition: height .3s ease-out;
    transition: height .3s ease-out;
}
.menu .treeview-menu a {
      position: relative;
      padding: 5px 20px 5px 10px !important;
      color: #8aa4af;
      display: inline-block;
      width: 100%;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      background: #2c3b41;
}
.menu .treeview-menu a:hover {
        color: white;
        border-left: none;
        background: #2c3b41;
}
.menu .treeview-menu .treeview-menu {
      padding-left: 20px;
}
.menu .treeview-menu .treeview {
      background: #1a2226 !important;
      -webkit-transition: none !important;
      transition: none !important;
}
.menu .treeview-menu .treeview a {
        -webkit-transition: none !important;
        transition: none !important;
}
.menu .openIcon {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transition: all ease .2s;
    transition: all ease .2s;
}



